import React, { useEffect, useState } from "react";
import {
  Avatar,
  Typography,
  Divider,
  CircularProgress,
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  TextField,
} from "@material-ui/core";
import { connect } from "react-redux";
import { load_user_detail } from "../actions/shop";

const UserDetail = ({ user, load_user_detail }) => {
  useEffect(() => {
    const fetchData = async () => {
      try {
        await load_user_detail(localStorage.getItem("id"));
      } catch (err) {}
    };
    if (localStorage.getItem("id")) {
      fetchData();
    }
  }, []);
  const [dialogOpen, setdialogOpen] = useState(false);
  const handleDialog = () => {
    setdialogOpen(!dialogOpen);
    setFormData({
      email: user.email,
      name: user.name,
      phone_no: user.phone_no,
      account_no: user.account_no,
      birth_date: user.birth_date,
      id_code: user.id_code,
    });
  };
  const [formData, setFormData] = useState({
    email: "",
    name: "",
    phone_no: "",
    account_no: "",
    birth_date: "",
    id_code: "",
  });

  const { email, name, phone_no, account_no, birth_date, id_code } = formData;
  const onChange = (e) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };
  const onSubmit = (e) => {
    e.preventDefault();
  };
  return user ? (
    <div style={{ margin: 20 }}>
      <Button
        color="secondary"
        variant="outlined"
        onClick={() => handleDialog()}
      >
        ویرایش
      </Button>
      <Avatar src="/broken-image.jpg" style={{ marginTop: 20 }} />

      <Typography variant="h5">ایمیل</Typography>
      <Typography variant="subtitle1">
        {user.email ? user.email : "--"}
      </Typography>
      <Divider />
      <Typography variant="h5">نام</Typography>
      <Typography variant="subtitle1">
        {user.name ? user.name : "--"}
      </Typography>
      <Divider />
      <Typography variant="h5">تلفن</Typography>
      <Typography variant="subtitle1">
        {user.phone_no ? user.phone_no : "--"}
      </Typography>
      <Divider />
      <Typography variant="h5">شماره کارت</Typography>
      <Typography variant="subtitle1">
        {user.account_no ? user.account_no : "--"}
      </Typography>
      <Divider />

      <Typography variant="h5">تاریخ تولد</Typography>
      <Typography variant="subtitle1">
        {user.birth_date ? user.birth_date : "--"}
      </Typography>
      <Divider />

      <Typography variant="h5">کد ملی</Typography>
      <Typography variant="subtitle1">
        {user.id_code ? user.id_code : "--"}
      </Typography>
      <Divider />

      <Dialog open={dialogOpen} onClose={handleDialog}>
        <DialogTitle>ویرایش اطلاعات</DialogTitle>
        <DialogContent>
          <form
            onSubmit={(e) => onSubmit(e)}
            style={{ marginLeft: 20, marginRight: 20 }}
          >
            <div>
              <TextField
                type="email"
                label="ایمیل"
                name="email"
                value={email}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                type="text"
                label="نام و نام خانوادگی"
                name="name"
                value={name}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                type="number"
                label="تلفن"
                name="phone_no"
                value={phone_no}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                type="number"
                label="شماره کارت"
                name="account_no"
                value={account_no}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                name="birth_date"
                label="تاریخ تولد"
                type="date"
                value={birth_date}
                onChange={(e) => onChange(e)}
                InputLabelProps={{
                  shrink: true,
                }}
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                type="number"
                label="کد ملی"
                name="id_code"
                value={id_code}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <Button
              type="submit"
              style={{ margin: 20 }}
              variant="contained"
              color="secondary"
            >
              تایید
            </Button>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  ) : (
    <CircularProgress color="secondary" />
  );
};
const mapStateToProps = (state) => ({
  user: state.shop.user,
});
export default connect(mapStateToProps, { load_user_detail })(UserDetail);
