import React, { useEffect, useState } from "react";
import { connect } from "react-redux";
import { load_address } from "../actions/shop";
import {
  /// Typography,
  Table,
  TableBody,
  TableCell,
  TableContainer,
  TableHead,
  TableRow,
  Radio,
  CircularProgress,
  Button,
  Dialog,
  DialogContent,
  DialogTitle,
  TextField,
  Checkbox,
  FormControlLabel,
} from "@material-ui/core";

const Addresses = ({ addresses, load_address }) => {
  useEffect(() => {
    const fetchData = async () => {
      try {
        await load_address(localStorage.getItem("id"));
      } catch (err) {}
    };
    if (localStorage.getItem("id")) {
      fetchData();
    }
  }, []);

  const [dialogOpen, setdialogOpen] = useState(false);
  const [formData, setFormData] = useState({
    id: "",
    address: "",
    zip_code: "",
    phone: "",
    defaultt: false,
  });
  const handleDialog = (index) => {
    setdialogOpen(!dialogOpen);
    setFormData({
      id: addresses[index].id,
      address: addresses[index].address,
      zip_code: addresses[index].zip_code,
      phone: addresses[index].phone,
      defaultt: addresses[index].default,
    });
  };

  const { id, address, zip_code, phone, defaultt } = formData;
  const onChange = (e) => {
    if (e.target.name === "defaultt") {
      setFormData({ ...formData, [e.target.name]: !defaultt });
    } else {
      setFormData({ ...formData, [e.target.name]: e.target.value });
    }
  };
  const onSubmit = (e) => {
    e.preventDefault();
  };
  return addresses ? (
    <div style={{ margin: 20 }}>
      <TableContainer>
        <Table aria-label="orders">
          <TableHead>
            <TableRow>
              <TableCell>آدرس</TableCell>
              <TableCell>کد پستی</TableCell>
              <TableCell>تلفن</TableCell>
              <TableCell>پیش فرض</TableCell>
              <TableCell>ویرایش</TableCell>
            </TableRow>
          </TableHead>
          <TableBody>
            {addresses.map((address, index) => (
              <TableRow>
                <TableCell>{address.address}</TableCell>
                <TableCell>{address.zip_code}</TableCell>
                <TableCell>{address.phone}</TableCell>
                <TableCell>
                  <Radio checked={address.default} />
                </TableCell>
                <TableCell>
                  <Button
                    color="secondary"
                    variant="outlined"
                    onClick={() => handleDialog(index)}
                  >
                    ویرایش
                  </Button>
                </TableCell>
              </TableRow>
            ))}
          </TableBody>
        </Table>
      </TableContainer>

      <Dialog open={dialogOpen} onClose={() => setdialogOpen(false)}>
        <DialogTitle>ویرایش اطلاعات</DialogTitle>
        <DialogContent>
          <form
            onSubmit={(e) => onSubmit(e)}
            style={{ marginLeft: 20, marginRight: 20 }}
          >
            <div style={{ marginTop: 5 }}>
              <TextField
                type="text"
                label="آدرس"
                name="address"
                value={address}
                multiline
                rows={4}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                type="number"
                label="کد پستی"
                name="zip_code"
                value={zip_code}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <TextField
                type="number"
                label="تلفن"
                name="phone"
                value={phone}
                onChange={(e) => onChange(e)}
                required
              />
            </div>
            <div style={{ marginTop: 5 }}>
              <FormControlLabel
                label="پیش فرض"
                control={
                  <Checkbox
                    name="defaultt"
                    onChange={(e) => onChange(e)}
                    checked={defaultt}
                  />
                }
              />
            </div>
            <Button
              type="submit"
              style={{ margin: 20 }}
              variant="contained"
              color="secondary"
            >
              تایید
            </Button>
          </form>
        </DialogContent>
      </Dialog>
    </div>
  ) : (
    <CircularProgress color="secondary" />
  );
};
const mapStateToProps = (state) => ({
  addresses: state.shop.address,
});
export default connect(mapStateToProps, { load_address })(Addresses);
